<template>
  <div class="main-content">
    <div class="image-wrapper">
      <Carousel autoplay v-model="current" loop>
        <CarouselItem>
          <div class="image-item"><img src="./pic1.jpeg" alt=""></div>
        </CarouselItem>
        <CarouselItem>
          <div class="image-item"><img src="./pic2.jpeg" alt=""></div>
        </CarouselItem>
        <CarouselItem>
          <div class="image-item"><img src="./pic3.jpg" alt=""></div>
        </CarouselItem>
        <CarouselItem>
          <div class="image-item"><img src="./pic4.jpg" alt=""></div>
        </CarouselItem>
      </Carousel>
    </div>
    <div class="call-paper">
      <!-- <p class="call-title">Call for Paper</p> -->
      <div class="call-content">
        <p><strong class="news">News</strong> <br></p>
        <ul>
          <li>(May 17, 2023) The conference will award 2 best paper awards (one certificate and RMB 1500 for each paper)! </li>
          <li>(May 26 , 2023) The conference proceedings publication was formally approved by Springer!</li>
          <li>
            (June 8, 2023）Selected papers from FCS 2023 will be invited to consider submission for special issues in Elsevier Journal of Information Security and Applications. Please see <a target="_blank" href="https://www.journals.elsevier.com/journal-of-information-security-and-applications/call-for-papers/special-issue-on-new-frontiers-in-cyber-security">https://www.journals.elsevier.com/journal-of-information-security-and-applications/call-for-papers/special-issue-on-new-frontiers-in-cyber-security</a>
            for details.
          </li>
        </ul>
        <!-- <p>
            (May 17, 2023) The conference will award 2 best paper awards (one certificate and RMB 1500 for each paper)! <br><br>
          </p> -->
        <p class="content">
          The First International Conference on Frontiers in Cyber Security (FCS 2023) will be held in Chengdu, P.R. China from November 5th to November 7th, 2023, organized by the Center for Cyber Security, University of Electronic Science and Technology of China
          (UESTC). The cyber security is paid more and more attention by industry and academia. New cyber security technologies are emerging. Many universities and institutions have set up the research center or school for cyber security. Both government
          and enterprises are strongly supporting teaching and research in the cyber security. However, how to carry out research and education on cyber security is inexperienced. How to run the center for cyber security is also worth exploring since new
          center usually involves many departments. The FCS conference provides a good platform for researchers and practitioners to exchange the latest research results in cyber security and management experience in operating such a center. The conference
          proceedings will be published by Springer Communications in Computer and Information Science (CCIS) which is indexed by EI.
        </p>
      </div>
    </div>
    <!-- <a-instructions></a-instructions> -->
    <!-- <area-interest></area-interest> -->
    <!-- <committee-index></committee-index> -->
    <fcs-footer></fcs-footer>
  </div>
</template>

<script>
  import AInstructions from '../../components/author-instruction/a-instructions.vue'
  import AreaInterest from '../../components/area-interest/area-interest.vue'
  import Committee from '../../components/committee/committee.vue'
  import CommitteeIndex from '../../components/committee/committee-index.vue'
  import Footer from '../footer/footer.vue'
  
  export default {
    components: {
      AInstructions,
      AreaInterest,
      Committee,
      CommitteeIndex,
      'fcs-footer': Footer
    },
    data() {
      return {
        current: 0
      }
    },
  }
</script>

<style scoped>
  .news {
    text-indent: 0;
    color: #f30;
  }
  
  ul {
    border-bottom: 1px solid #000;
    padding: 20px 10px;
  }
  
  li {
    list-style: disc;
    text-indent: 0;
    padding-left: 2rem;
  }
  
  .main-content {
    margin: 10px auto;
  }
  
  .image-wrapper {
    width: 70%;
    height: 40%;
    margin: 10px auto;
  }
  
  .image-item {
    width: 100%;
  }
  
  .content {
    margin-top: 30px;
  }
  
  .image-wrapper img {
    width: 100%;
    height: 350px;
  }
  
  .call-paper {
    margin: 10px auto;
    width: 70%;
    margin-top: 20px;
    border-radius: 4px;
    box-sizing: border-box;
    border: 1px solid #ccc;
    background-color: #ddd;
    text-align: center;
    padding: 20px;
  }
  
  .call-title {
    font-size: 24px;
    line-height: 36px;
  }
  
  .call-content {
    font-size: 18px;
    line-height: 24px;
    width: 95%;
    margin: 10px auto;
    text-align: left;
    text-indent: 2em;
  }
</style>


